// Message types
interface Message {
  id: string
  role: 'user' | 'assistant' | 'system'
  content: string
  timestamp: Date
  status?: 'sending' | 'sent' | 'error'
  actions?: ActionItem[]
  crmData?: CRMData
}

interface ActionItem {
  id: string
  type: 'task' | 'workflow' | 'contact' | 'email' | 'sms' | 'appointment' | 'pipeline' | 'report' | 'automation'
  title: string
  status: 'pending' | 'completed' | 'in_progress' | 'scheduled'
  details?: string
  link?: string
}

interface CRMData {
  type: 'contacts' | 'deals' | 'tasks' | 'analytics' | 'workflows'
  items?: { name: string; value: string | number; trend?: string }[]
  chart?: { type: string; data: number[] }
}

